'use strict';

// MODULES //

import isArray from '@stdlib/assert-is-array';
import { isPrimitive as isNumber } from '@stdlib/assert-is-number';
import isObject from '@stdlib/assert-is-plain-object';
import isnan from '@stdlib/assert-is-nan';
import hasOwnProp from '@stdlib/assert-has-own-property';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination for validated options
* @param {Options} options - function options
* @param {number} [options.alpha] - significance level
* @param {Array} [options.groups] - array of group indicators
* @returns {(null|Error)} null or an error
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '0th2V,FD', options ) );
	}
	if ( hasOwnProp( options, 'alpha' ) ) {
		opts.alpha = options.alpha;
		if ( !isNumber( opts.alpha ) || isnan( opts.alpha ) ) {
			return new TypeError( format( '0th8P,GU', 'alpha', opts.alpha ) );
		}
	}
	if ( hasOwnProp( options, 'groups' ) ) {
		opts.groups = options.groups;
		if ( !isArray( opts.groups ) ) {
			return new TypeError( format( '0th8Z,GX', 'groups', opts.groups ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
